import styled from "styled-components";

import { colors, displayFontSizes, Margins } from "../styleguide";

import { RadioLabelProps, StyledRadioProps } from "./Radio.types";

export const RadioWrapper = styled.div<Margins>`
  display: flex;
  align-items: center;
  font-family:  ${displayFontSizes.bodyCopy.$fontFamily};
  font-size: ${displayFontSizes.bodyCopy.$fontSize};
  line-height: ${displayFontSizes.bodyCopy.lineHeight};
  ${({ mt }) => mt && { marginTop: `${mt}` }}
  ${({ mb }) => mb && { marginBottom: `${mb}` }}
  ${({ mr }) => mr && { marginRight: `${mr}` }}
  ${({ ml }) => ml && { marginLeft: `${ml}` }}
  ${({ m }) => m && { margin: `${m}` }}
`;

export const Label = styled.label<RadioLabelProps>`
  cursor: ${({ isDisabled }) => isDisabled ? "not-allowed" : "pointer"};
  opacity: ${({ isDisabled }) => isDisabled ? 0.33 : 1};
  margin-left: 22px;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    left: -30px;
    box-sizing: border-box;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    background-color: ${({ isDisabled }) => {
		if (isDisabled) {
			return colors.lightestGray;
		}
		return colors.white;
	}};
    border-style: solid;
    cursor: ${({ isDisabled }) => isDisabled ? "not-allowed" : "pointer"};
    border-width: ${({ isChecked }) => isChecked ? "2px" : "1px"};
    border-color: ${({ isChecked, isInvalid }) => {
		if (isChecked) {
			if (isInvalid) { return colors.red; }
			return colors.midBlue;
		}
		if (isInvalid) { return colors.red; }

		return colors.defaultGray;
	}};
  }

  &::after {
    content: '';
    position: absolute;
    left: -26px;
    top: 4px;
    border-radius: 50%;
    box-sizing: border-box;
    width: 12px;
    height: 12px;
  }
`;

export const RadioInput = styled.input<StyledRadioProps>`
	opacity: 0;
  appearance: none;
  &:checked + ${Label}::after {
      background-color: ${({ checked, isInvalid }) => {
		if (checked && isInvalid) {
			return colors.red;
		} if (checked && !isInvalid) {
			return colors.midBlue;
		}
		return colors.white;
	}};
  }

  &:hover + ${Label}::before {
    border-color: ${({ checked, disabled, isInvalid }) => {
		if (!disabled) {
			if (!checked) {
				return colors.black;
			} if (isInvalid) {
				return colors.red;
			}
			return colors.midBlue;
		}
		if (isInvalid) {
			return colors.red;
		} if (!checked) {
			return colors.defaultGray;
		}
		return colors.midBlue;
	}};
  }

  &:focus-within + ${Label}::before {
    box-shadow: 0 0 0 4px ${colors.midBlue}40;
  }
`;
